.select {
  position: relative;
  width: 100%;
  min-width: 160px;
  user-select: none;
}

.select.active .select__body {
  height: auto;
  max-height: 500px;
}

.select.active .mobile-header__icon {
  transform: scaleY(1) !important;
}
.select.active .select__icon {
  transform: scaleY(-1);
}
.select.active .select__body {
  z-index: 20;
  display: block;
}
.select__header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 65px;
  min-width: 85px;
  height: 25px;
  background-color: inherit;
  cursor: pointer;
  grid-gap: 8px;
}
.select__header::before {
  content: "";
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: var(--bg-b);
}

.select__current {
  font-style: normal;
  font-weight: 700;
  font-size: 17px;
  line-height: 160%;
  font-family: var(--font-family);
  color: var(--bg-b);
}

.select__icon {
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 13px;
  height: 6px;
  text-align: center;
  transform: scaleY(1);
  transition: all 0.4s;
}

.select__body {
  position: absolute;
  right: 0;
  left: 0;
  z-index: 80;
  display: none;
  width: 100%;
  max-height: 0;
  margin-top: 10px;
  padding: 0;
  overflow: hidden;
  border-top: none;
}

.select__item {
  width: 100%;
  margin: 0 auto;
  padding: 0 8px;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 160%;
  font-family: var(--font-family);
  color: var(--color-b);
  cursor: pointer;
  opacity: 0.8;
  transition: all 0.4s;
  user-select: none;
}
.select__item:hover {
  opacity: 1;
}

.select__wrapp {
  z-index: 100;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 12px 0 10px;
  background-image: url(/img/index/bg1.png);
  border: 1px solid var(--color-b);
  border-radius: 7px;
  grid-gap: 8px;
}

.select__item:hover {
  color: var(--color-e);
}

