body {
  position: relative;

  display: flex;
  flex-direction: column;
  width: 100%;
  width: 100%;
  min-height: -webkit-fill-available;
  margin: 0 auto;
  padding: 0;
  overflow-x: hidden;

  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  font-family: inherit;
  color: var(--color-b);

  background-color: var(--body-bg);
  cursor: url(../../img/cur.png),auto!important;

  @include tablet {
    width: 100%;
    overflow-x: hidden;
  }
  -webkit-text-size-adjust: 100%;

  &.fixed {
    position: fixed;
    top: 0;
    left: 0;

    width: 100%;
    overflow: hidden;
  }
}



footer {
  flex: 0 0 auto;
}

html {
  box-sizing: border-box;
  width: 100%;
  overflow-x: hidden;

  font-weight: 400;
  font-size: 16px;
  line-height: normal;
  font-family: var(--font-family);

  @include tablet {
    width: 100%;
    overflow-x: hidden;
  }
  scroll-behavior: smooth;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
  display: inline-flex;
  margin: 0;

  font-style: inherit;
  font-weight: inherit;
  font-family: inherit;
  color: inherit;
  text-decoration: none;

  cursor: pointer;
}

main {
  flex: 1 0 auto;
}

button {
  display: inline-flex;
  margin: 0;
  padding: 0;

  line-height: initial;

  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

ul,
li {
  margin: 0;
  padding: 0;

  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;

  object-fit: cover;
}


picture {
  display: inline-flex;
}

label {
  display: inline-flex;
}

input {
  width: 100%;

  border: none;
  outline: none;
}

textarea {
  border: none;
  outline: none;

  resize: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;

  appearance: none;
}

input[type="number"] {
  appearance: textfield;
}


.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;

  white-space: nowrap;

  border: 0;

  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  position: relative;

  width: 100%;
  max-width: var(--container-width);
  margin: 0 auto;
  padding: 0 var(--container-offset);

  &--mode {
    max-width: var(--container-width-mode);
  }

  &--mini {
    max-width: var(--container-width-mini);
  }
}




.panel,
[data-content] {
  z-index: 20;

  max-height: 0;
  overflow: hidden;

  transition: 0.2s ease-out;
}

[data-tab-content] {
  display: none;

  &.active {
    display: block;
  }
}

.dis-scroll {
  position: fixed;

  width: 100%;
  overflow: hidden;
}

* {
  &::-webkit-scrollbar {
    width: 7px;
    height: 7px;

    cursor: pointer;

    @include small-tablet {
      display: none;
    }
  }

  &::-webkit-scrollbar-thumb {
    background-color: var(--bg-b);
    cursor: pointer;

    transition: all 0.4s;;

    &:hover {
      background-color: var(--color-e);
    }
  }

  &::-webkit-scrollbar-track {
    background-color: transparent;

    &:hover {
      background-color: transparent;
    }
  }

  &::-webkit-scrollbar-button {
    display: none;

    background-color: transparent;
    box-shadow: none;
  }
}
